.elements{
    display: grid;
    max-width: 882px;
    gap: 20px 17px;
    grid-template-columns: repeat(auto-fit,282px);
    justify-content: center;
    margin: 47.25px auto 67px auto;
}
@media screen and (max-width: 700px) {
    .elements{
        margin: 36px auto 48px auto;
    }
}