.profile__title {
  margin: 0 18px 0 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #ffffff;
  font-family: Inter, Arial, sans-serif;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 500px;
}
@media screen and (max-width: 920px){
  .profile__title{
    max-width: 400px;
  }
}
@media screen and (max-width: 700px) {
  .profile__title {
    font-size: 27px;
    line-height: 33px;
    text-align: center;

  }
}
@media screen and (max-width: 480px){
  .profile__title{
    max-width: 280px;
  }
}